import * as React from 'react';
import { NavLink } from 'react-router-dom';
import Label from '../../atoms/Label';
import Ul from './styles';

interface SubMenu {
  label: string;
  path: string;
}

interface Menu {
  label: string;
  path?: string;
  subMenu?: SubMenu[];
}

const Menu: React.FC = () => {
  const menus: Menu[] = [
    { label: 'Top', path: '/react-study/top' },
    { label: 'Props', path: '/react-study/props' },
    {
      label: 'State',
      subMenu: [
        { label: 'State1', path: '/react-study/state1' },
        { label: 'State2', path: '/react-study/state2' }
      ]
    },
    {
      label: 'Redux',
      subMenu: [
        { label: 'State1', path: '/react-study/state1' },
        { label: 'State2', path: '/react-study/state2' }
      ]
    }
  ];

  return (
    <Ul>
      {menus.map(({ label, path, subMenu }) => (
        <li key={label}>
          {path !== undefined ? (
            <NavLink className="nav-link" to={path}>
              {label}
            </NavLink>
          ) : (
            <Label isCursorPointer={true}>{label}</Label>
          )}
          {subMenu && (
            <ul className="sub-menu">
              {subMenu.map(({ label: label2, path: path2 }) => (
                <li key={label2}>
                  <NavLink className="nav-link" to={path2}>
                    {label2}
                  </NavLink>
                </li>
              ))}
            </ul>
          )}
        </li>
      ))}
    </Ul>
  );
};

export default Menu;
