import styled from 'styled-components';

const Ul = styled.ul`
  background: rgb(29, 33, 19);
  display: flex;
  justify-content: space-between;
  padding: 0;
  position: relative;
  width: 100%;
  height: 50px;
  max-width: 1000px;
  margin: 0 auto;
  list-style: none;

  > li {
    float: left;
    height: 50px;
    line-height: 50px;
    padding-left: 20px;
    position: relative;
    width: inherit;

    .nav-link {
      color: #fff;
      display: block;
      position: relative;
      text-decoration: none;
    }

    > p::after {
      content: '';
      display: block;
      width: 6px;
      height: 6px;
      margin-left: 15px;
      border-right: 1px solid #fff;
      border-bottom: 1px solid #fff;
      transform: rotate(45deg);
    }

    .sub-menu {
      display: none;
      padding: 0;
      position: absolute;
      left: 0px;
      top: 50px;
      transition: 0.5s;
      width: 100%;
      z-index: 2;

      > li {
        list-style: none;
        padding-left: 20px;

        .nav-link {
          display: block;
        }

        &:hover {
          background: #125a64;
        }
      }
    }

    &:hover > .sub-menu {
      background: #072a24;
      display: block;
      height: calc(100% + 50px);
    }
  }
`;

export default Ul;
